@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.col-1 {
  /* border: 10px solid black; */
  height: 100%;
}

.Container {
  font-family: "Montserrat", sans-serif;
  height: 100vh;
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.left-top {
  height: 50vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #7164ff;
  padding: 50px 40px;
}

.left-top .title-div {
  height: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-transform: uppercase;
  letter-spacing: 10px;
  color: #fff;
  font-weight: bold;
}

.left-top .image-div {
  height: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.left-bot {
  height: 50vh;
  background-color: #6155db;
  padding: 50px 40px;
}

.left-bot .heading-div {
  height: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-size: 30px;
  letter-spacing: 5px;
  text-transform: capitalize;
}

.left-bot .image-div {
  padding: 40px 0px;
  height: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.left-bot .image-div .box {
  width: 50%;
  /* border: 2px solid white; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px 20px;
  color: #fff;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.left-bot .image-div .box .icon {
  margin-bottom: 20px;
}

.col-2 {
  height: 100vh;
}

.right-top {
  height: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 100px 30px 10px 30px;
  -webkit-align-items: center;
          align-items: center;
}

.right-top .heading-div {
  font-size: 30px;
}

.right-bottom {
  background-color: whitesmoke;
  height: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px 40px;
  overflow-y: scroll;
}

.right-bottom .card {
  /* border: 1px solid; */
  margin-bottom: 20px;
}

.right-bottom .card .card-top {
  /* border: 1px solid; */
  display: -webkit-flex;
  display: flex;
  padding: 20px 20px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.right-bottom .card .card-top .heading-flex {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 10px;
}

.right-bottom .card .card-top .arrow {
  margin-left: auto;
  padding: 10px 10px;
  cursor: pointer;
}

.right-bottom .card .card-top .square {
  height: 70px;
  width: 70px;
  background-color: orange;
}

.right-bottom .card .card-bot {
  /* border: 1px solid; */
  display: -webkit-flex;
  display: flex;
  padding: 20px 20px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

@media only screen and (max-width: 500px) {
  .left-bot {
    height: 100%;
  }
  .left-bot .image-div .box {
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 20px;
  }
}

.heading-div-2 {
  background-color: #7164ff;
  width: "100%";
  padding: 35px 50px;
  text-transform: uppercase;
  letter-spacing: 10px;
  color: #fff;
  font-weight: bold;
}

.title-div-2 {
  display: -webkit-flex;
  display: flex;
  padding: 20px 20px;
  cursor: pointer;
}

.title-div-2 .square-div {
  display: -webkit-flex;
  display: flex;
}

.title-div-2 .square-div .square {
  height: 70px;
  width: 70px;
  background-color: orange;
  margin-left: 30px;
}

.heading-flex {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 2px 10px;
  font-size: 1rem;
}

@media screen and (max-width: 425px) {
  .title-div-2 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0;
  }
  .title-div-2 .square-div {
    padding: 20px 0px;
  }
  .title-div-2 .square-div .square {
    margin-left: 0;
  }
}

